import React, { Component } from 'react'

const Note = ({ deleteMethod, toggleMethod, text, completed }) => {
  const classString = completed ? 'note completed' : 'note'
    return (
      <div
        className={classString}
        onClick={toggleMethod}
        onDoubleClick={deleteMethod}>
        {text}
        <button>Edit</button>
        <button>Done</button>

      </div>
    )
  }

export default Note
